import Vue from 'vue'
import VueRouter from 'vue-router'

import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: () => import(/* webpackChunkName: "home" */ '../views/Home.vue'),
    meta: {
      title: 'juriel sean',
    }
  },
]

const router = new VueRouter({
  routes,
  mode: 'history',
})

router.beforeEach((to, from, next) => {
  document.title = `${to.meta.title} | ${to.hash ? to.hash : 'fullstack developer'}`;
  let hasContent = store.getters.hasPageContent(to.hash);
  if (!hasContent) {
    next('#landing');
  } else {
    next();
  }
})

export default router
