<template>
  <v-app>
    <v-container
      class="app-container"
      fluid
      fill-height>
      <!-- Naviation bar -->
      <div class="pa-0 ma-0 h-100">
        <v-navigation-drawer
          ref="mainDrawer"
          class="accent side-nav d-flex justify-content-center"
          permanent
          fixed
          :mini-variant="mini">
          <v-list
            class="ma-0 pa-0 w-100 d-flex align-content-center flex-wrap">
            <v-list-item
              class="px-2 w-100"
              v-for="(item, index) in sideNavItems"
              :key="index"
              @click="toggleSideNav()"
              :href="item.toRoute"
              link>
              <div class="d-flex flex-row">
                <v-list-item-avatar v-if="item.isImage">
                  <img :src="item.icon">
                </v-list-item-avatar>
                <v-list-item-avatar v-else>
                  <v-icon large> {{ item.icon }} </v-icon>
                </v-list-item-avatar>
                <v-list-item-content>
                    <v-list-item-title>
                      {{ item.title }}
                    </v-list-item-title>
                </v-list-item-content>
              </div>
            </v-list-item>
          </v-list>
        </v-navigation-drawer>
      </div>
      <!-- End Navigation bar -->

      <v-content
        ref="contentContainer"
        id="content-container"
        class="vh-100"
        v-bind:style="contentContainerStyle">
        <transition>
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </transition>
      </v-content>
    </v-container>
  </v-app>
</template>

<script>
import { actionTypes } from './store/landing/actions'
import { mapGetters } from 'vuex'

export default {
  name: 'App',

  components: {
  },

  computed: {
    ...mapGetters([
      'isSmallWidthMode',
      'windowDimensions',
      'sideNavItems'
    ]),

    mini() {
      return !this.navIsOpen && this.isSmallWidthMode;
    },
  },

  data: () => ({
    loading: true,
    navIsOpen: false,
    contentContainerStyle: {
      paddingLeft: '256px'
    }
  }),

  created() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.windowResized);
      this.windowResized();
    })
  },

  beforeDestroy() {
    window.removeEventListener('resize', this.windowResized);
  },

  methods: {
    toggleSideNav() {
      // create store action
      if (this.isSmallWidthMode) {
        this.navIsOpen = !this.navIsOpen;
      }
    },
    
    windowResized() {
      let windowDimensions = {
        height: document.documentElement.clientHeight,
        width: document.documentElement.clientWidth
      };

      this.$store.dispatch(actionTypes.setWindowDimensions, windowDimensions)
        .then(() => {})
        .catch(() => {});

      this.contentContainerStyle = {
        paddingLeft: `${ this.isSmallWidthMode ? 56 : this.$refs['mainDrawer'].width }px`
      };

      if (!this.isSmallWidthMode) {
        this.toggleSideNav();
      }
    },

    test() {
      console.log('click out');
    }
  },
};
</script>

<style>
html {
  height: 100%;
  overflow-y: auto;
}

body {
  height: 100%;
  margin: 0 auto 0;
}

.app-container {
  padding: 0 !important;
  margin: 0 !important;
}

.vh-100 {
  height: 100vh;
}

.h-100 {
  height: 100%;
}

.w-100 {
  width: 100%;
}

.v-navigation-drawer__content {
  display: flex !important;
  justify-content: center;
  flex-wrap: wrap;
}
</style>

<style scoped>
.side-nav {
  width: 30%;
  max-width: 500px;
}

.list-item {
  color: #f0f0f0;
}
</style>