export default {
  windowDimensions: state => state.windowDimensions,
  isSmallWidthMode: state => state.windowDimensions.width < 750,
  sideNavItems: state => state.sideNavItems,
  pageContents: state => state.pageContents,
  pageContent: state => route => state.pageContents.find(pc => pc.anchor === route),
  hasPageContent: state => route => !!state.pageContents.find(pc => pc.anchor === route),
  contactMeDetails: state => state.contactMeDetails,
}